import { createClient } from '@supabase/supabase-js';
import type { Card } from '../types';
import type { Database } from '../types/database.types';

const supabaseUrl = process.env.REACT_APP_SUPABASE_URL;
const supabaseKey = process.env.REACT_APP_SUPABASE_ANON_KEY;

if (!supabaseUrl || !supabaseKey) {
  throw new Error('Missing Supabase environment variables');
}

export const supabase = createClient<Database>(supabaseUrl, supabaseKey);

export async function saveCard(card: Omit<Card, 'id'> & { spells: number[], tags: number[] }) {
  console.log('Card input:', JSON.stringify(card, null, 2));

  try {
    // Check if we're dealing with a CardFrontend (camelCase) or Card (snake_case)
    const cardData = {
      name: card.name,
      description: card.description,
      type: card.type,
      rarity: card.rarity,
      health: card.health,
      image: card.image || null,
      // Handle both possible property namings
      passive_effect: 'passiveEffect' in card ? card.passiveEffect : card.passive_effect,
      updated_at: new Date().toISOString(),
      is_wip: 'isWIP' in card ? card.isWIP : card.is_wip,
      is_crap: 'isCrap' in card ? card.isCrap : card.is_crap
    };

    console.log('Saving card data:', JSON.stringify(cardData, null, 2));

    const { data, error } = await supabase
      .from('cards')
      .insert([cardData])
      .select();

    if (error) {
      console.error('Erreur de sauvegarde:', error);
      console.error('Données envoyées qui ont causé l\'erreur:', JSON.stringify(cardData, null, 2));
      throw new Error(`Erreur lors de la sauvegarde: ${error.message}`);
    }

    const cardId = data[0].id;

    // Save spells
    if (card.spells?.length > 0) {
      const spellData = card.spells.map((spellId: number) => ({ card_id: cardId, spell_id: spellId }));
      const { error: spellError } = await supabase
        .from('card_spells')
        .insert(spellData);
      if (spellError) throw spellError;
    }

    // Save tags
    if (card.tags?.length > 0) {
      const tagData = card.tags.map((tagId: number) => ({ card_id: cardId, tag_id: tagId }));
      const { error: tagError } = await supabase
        .from('card_tags')
        .insert(tagData);
      if (tagError) throw tagError;
    }

    return data;
  } catch (e) {
    console.error('Exception lors de la sauvegarde:', e);
    throw e;
  }
}

export async function getAllCards() {
  const { data, error } = await supabase
    .from('cards')
    .select('*')
    .order('updated_at', { ascending: false });

  if (error) throw error;

  // Convertir le format des données et s'assurer que les tableaux sont initialisés
  return (data || []).map(card => ({
    ...card,
    passiveEffect: card.passive_effect,
    isWIP: card.is_wip ?? true, // Si is_wip est null, on considère la carte comme WIP
    isCrap: card.is_crap ?? false,
    passive_effect: undefined,
    is_wip: undefined,
    is_crap: undefined
  })) as Card[];
}

export async function searchCards(query: string) {
  const { data, error } = await supabase
    .from('cards')
    .select('*')
    .or(`name.ilike.%${query}%,description.ilike.%${query}%,type.ilike.%${query}%`)
    .order('updated_at', { ascending: false });

  if (error) throw error;

  // Réutiliser la même logique de conversion que dans getAllCards
  return (data || []).map(card => ({
    ...card,
    passiveEffect: card.passive_effect,
    isWIP: card.is_wip ?? true, // Si is_wip est null, on considère la carte comme WIP
    isCrap: card.is_crap ?? false,
    passive_effect: undefined,
    is_wip: undefined,
    is_crap: undefined
  })) as Card[];
}

export async function deleteCard(id: string) {
  const { error } = await supabase
    .from('cards')
    .delete()
    .eq('id', id);

  if (error) throw error;
}

export async function getAutocompleteValues() {
  const { data: cards, error: cardsError } = await supabase
    .from('cards')
    .select('name, description, image, passive_effect');

  if (cardsError) throw cardsError;

  const suggestions = {
    names: Array.from(new Set(cards?.map(card => card.name) || [])),
    descriptions: Array.from(new Set(cards?.map(card => card.description).filter(Boolean) || [])),
    images: Array.from(new Set(cards?.map(card => card.image).filter(Boolean) || [])),
    passive_effects: Array.from(new Set(cards?.map(card => card.passive_effect).filter(Boolean) || []))
  };

  return suggestions;
}